import React, { useEffect } from 'react'
import SectionHead from './elements/SectionHeading'
import Slide from './elements/Slide'
import { ContainerFluid } from '../globalStyle'
import { slidesData } from '../data/SlidesData'
import styled from 'styled-components'
import gsap from 'gsap'

const SliderBody = styled.div`
    min-width: ${({ slidesCount }) => slidesCount ? slidesCount+'00%' : '100%'};
    display: flex;
    align-items: center;
    min-height: 90vh;
    max-height: 90vh;
    flex-wrap: nowrap;
`

const Slider = () => { 

    const slideAnimation = (timeline, slides) => {
        for(let i = 1; i <= slides; i++) {
            timeline.to(`.slide-${i} .back`, {width: '0'}, i - 1)
            timeline.to(`.slide-${i} ._slide-line`, {width: '100%'}, i - 1)
            timeline.from(`.slide-${i}`, {opacity: 0}, i - 1)
            timeline.from(`.slide-${i} .slide__image`, {scale: 0}, i - 1)
        }
    }

    useEffect(() => {

        let slides = gsap.utils.toArray(".slide");

        let totalSlides = slides.length

        if(totalSlides < 1) totalSlides = 1

        const sliderTimeline = gsap.timeline({
            scrollTrigger: {
                trigger: ".slider-section__body",
                pin: true,
                scrub: 1,
                start: "-=50",
                end: `+=${totalSlides}000`
            }
        })

        sliderTimeline.to(slides, {
            xPercent: (-100 * (slides.length - 1)) + (-18 * (slides.length - 1)),
            ease: "none"
        }, 0);

        const sliderTimeline2 = gsap.timeline({
            defaults: {ease: 'none'},
            scrollTrigger: {
                trigger: ".slider-section",
                scrub: 1,
                start: "-=500",
                end: 'bottom bottom',
            }
        })
        
        slideAnimation(sliderTimeline2, totalSlides)
        

    }, [])

    return (
        <div className="slider-section section">
            <ContainerFluid className="_container">
                <SectionHead offset={80}>
                До Ваших послуг персональні тренери - діючі плавці, призери України та Європи
                </SectionHead>
                <SliderBody slidesCount={slidesData.length} className="slider-section__body">
                    {slidesData.map(data =>  <Slide id={data.id} data={data} key={data.id} />)}
                </SliderBody>
            </ContainerFluid>
        </div>
    )
}

export default Slider