import React, { useEffect, useRef } from 'react'
import bubbles1 from '../img/icons/drops-1.png'
import bubbles2 from '../img/icons/drops-2.png'
import bubbles3 from '../img/icons/drops-3.png'
import swimLogo from '../img/icons/swim-circle.svg'
import swimWhite from '../img/icons/swim-2.svg'
import gsap from 'gsap'

const BubblesBlock = () => {
    const bubblesBlockRef = useRef()
    const strLineRef = useRef()
    const swimLogoRef = useRef()
    const bubblesRef1 = useRef()
    const bubblesRef2 = useRef()
    const bubblesRef3 = useRef()
    
    useEffect(() => {
        const tl = gsap.timeline({
            scrollTrigger: {
                trigger: bubblesBlockRef.current,
                start: 'top top',
                end: '+=3000',
                scrub: 1,
                pin: true
            }
        })

        tl.to(strLineRef.current, {
            xPercent: -100
        }, 0)

        tl.to(swimLogoRef.current, {
            scale: 1
        }, 0)

        gsap.to(bubblesRef1.current, {
            opacity: 1,
            scrollTrigger: {
                trigger: bubblesBlockRef.current,
                start: 'top top',
                end: '+=3000',
                scrub: 1
            }
        }, 0)
        gsap.to(bubblesRef2.current, {
            opacity: 1,
            scrollTrigger: {
                trigger: bubblesBlockRef.current,
                start: 'top top',
                end: '+=500',
                scrub: 1
            }
        }, 0)
        gsap.to(bubblesRef3.current, {
            opacity: 1,
            scrollTrigger: {
                trigger: bubblesBlockRef.current,
                start: 'top top',
                end: '+=200',
                scrub: 1
            }
        }, 0)

    }, [])

    return (
        <div ref={bubblesBlockRef} className="bubbles-block">
            <div className="bubbles-block__wrapper">
                <div className="bubbles-block__swim-logo">
                    <img ref={swimLogoRef}  src={swimLogo} alt="Swim System" />
                </div>
                <div className="bubbles-block__bubbles">
                    <img ref={bubblesRef1} className="bubbles" src={bubbles1} alt="Swim System" />
                    <img ref={bubblesRef2} className="bubbles" src={bubbles2} alt="Swim System" />
                    <img ref={bubblesRef3} className="bubbles" src={bubbles3} alt="Swim System" />
                </div>
                <div ref={strLineRef} className="bubbles-block__string-line">
                    <span>swim <img src={swimWhite} alt="Swim System" /> system - swim <img src={swimWhite} alt="Swim System" /> system</span>
                </div>
            </div>
        </div>
    )
}

export default BubblesBlock
